<template>
    <div>
        <b-carousel
            id="carousel-1"
            v-model="slide"
            :interval="4000"
            controls
            indicators
            background="#ababab"
            img-width="1024"
            img-height="731"
            style="text-shadow: 1px 1px 2px #333;"
            >
            <!-- Text slides with image -->
            <!-- Slides with custom text -->
            <b-carousel-slide :img-src="require('../assets/banner_home2.jpg')">
                <h1>Hello world!</h1>
            </b-carousel-slide>
            <b-carousel-slide :img-src="require('../assets/banner_home3.jpg')">
                <h1>Hello world!</h1>
            </b-carousel-slide>
            <!-- Slides with img slot -->
            <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->

            <!-- Slide with blank fluid image to maintain slide aspect ratio -->

        </b-carousel>
    </div>
  </template>

<script>
export default {
  name: 'Banners',
  data () {
    return {
      slide: ''
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .carousel-item {
    overflow: hidden;
  }
  </style>



